@import url(https://fonts.googleapis.com/css2?family=Balsamiq+Sans:wght@700&family=Roboto&family=Zen+Kaku+Gothic+Antique:wght@300&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

body {
  background: #b993d6;
  background: linear-gradient(to right, #8ca6db, #b993d6);
}
.App {
  margin-top: 13%;
  margin-left: 36%;
  font-family: "Balsamiq Sans", cursive;
  font-family: "Roboto", sans-serif;
  font-family: "Zen Kaku Gothic Antique", sans-serif;
  font-weight: bolder;
  font-size: larger;
  width: 30%;
  border-radius: 10px 10px 0 0;
  outline: none;
  background-color: #dec0e5;
  border: 2px solid rgb(43, 3, 77);
}
.display {
  width: 100%;
  height: 70px;
  border-radius: 10px 10px 0 0;
  display: flex;
  flex-direction: column;
  align-content: center;
  justify-content: space-evenly;
  border: none;
  outline: none;
  background-color: #ece1f5;
}
.flex-container {
  display: flex;
  justify-content: center;
  align-items: center;
}
.wrap {
  flex-wrap: wrap;
}
.flex-item {
  border-radius: 10px;
  border: solid 2px #5b67cf;
  background: #8ca6db;
  width: 20%;
  margin: 10px;
  color: white;
  font-weight: bold;
  font-size: 2em;
  text-align: center;
}

